@import "../../common/styles/sass/variables";

.projects_block{
    min-height: 100vh;
    border-bottom: 0.5px solid #ffffff;
    .projects_container{
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        flex-wrap: wrap;
        .projects{
            width: 100%;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            flex-wrap: wrap;
            @media screen and (max-width: 629px ){
                .projects{
                    justify-content: center;
                }
            }
        }
    }
}





